
import DialogTip from '@/components/dialog/dialog' 
import HeaderNav from '@/components/header/header'
import FootedTab from '@/components/footed/footed-tab'
import ListBox from '@/components/list/list-box'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { PostEnroll } from '@/api/api'
import { isEmail, isIntNumber, isPhone } from '@/tools/common'
import { wxShare } from '@/tools/wxShare'

export default {
components: {
XButton,
Toast,
ListBox,
FootedTab,
HeaderNav,
DialogTip
},
data() {
return {
form: {},
isDisabled: false,
isShowDialog: false
}
},
created() {
window.wxShare()
this.phone = isPhone
},
methods: {
onBackIntroduce() {
this.$router.push({ path: '/introduce' });
},
showLoadTip() {
this.$vux.loading.show({ text: '提交中' })
},
hideLoadTip() {
this.$vux.loading.hide()
},
_showToolTip(text, timeout = 2500) {
this.$vux.toast.text(text)
},
formRules(form) {
return new Promise((resolve, reject) => {
if (!form.product_name) {
reject("请输入产品名称")
}
if (!form.contact_name) {
reject("请输入联系人")
}
if (!form.phone) {
reject("请输入手机号")
}
if (!isIntNumber(form.phone)) {
reject("手机号必须为数字")
}
if (form.phone.length < 11 || form.phone.length > 11) {
reject("手机号需为11位数字")
}
if (!form.email) {
reject("请输入邮箱")
}
if (!isEmail(form.email)) {
reject("邮箱格式不正确！")
}

resolve("符合表单规则")
})
},
onSubmit() {
this.formRules(this.form).then((data) => {
console.info("表单规则验证成功", data); 

let option = {
...this.form
}
this.postEnrollApi(option)
}).catch((err) => {
this._showToolTip(err)
console.error("表单规则验证失败", err); 
})

},
async postEnrollApi(option) {
this.isDisabled = true
try {
const { data } = await PostEnroll(option)
this.isDisabled = false
this.hideLoadTip()
this.isShowDialog = true
console.log("提交报名成功", data)
} catch (err) {
this.isDisabled = false
this._showToolTip(err)
console.error("提交报名失败", err)
}
},
onCancelDialog() {
this.isShowDialog = false
setTimeout(() => {
this.$router.push({ path: '/introduce' });
}, 500)
}
}
}



