export default {
  name: 'list-box',
  props: {
    title: {
      default: '',
      type: String
    },
    content: {
      default: '',
      type: String
    },
    isShow: {
      default: false,
      type: Boolean
    }
  },
  data() {
    return {
      showSynopsis: true,
    }
  },
  created() {
  },
  methods: {
    onShowSynopsis() {
      this.showSynopsis = !this.showSynopsis
    }
  }
}



