export default {
  props: {
    showClose: {
      default: true,
      type: Boolean
    },
    title: {
      default: '',
      type: String
    },
    content: {
      default: '',
      type: String
    },
    tip: {
      default: '',
      type: String
    },
    confirmButtonText: {
      default: '',
      type: String
    },
    type: {
      default: 'success',
      type: String
    }
  },
  data() {
    return {
    }
  },
  computed: {
  },
  methods: {
    onClose() {
      this.showClose = false
      this.$emit('close')
    },
    onConfirm() {
      this.$emit('confirm')
    },
  }
}



