

export default {
name: 'header-nav',
props: {
goBack: {
default: true,
type: Boolean
},
headTitle: {
default: '报名表',
type: String
},
smallTitle: {
default: '',
type: String
}
},
data() {
return {
selected: 1,
tabs: [
{
id: 1,
title: "大赛介绍",
path: "/introduce"
}, {
id: 2,
title: "奖项设置",
path: "/awards"
}, {
id: 3,
title: "我要报名",
path: "/enroll"
}
]
}
},
created() {
this.selected = this.index
},
methods: {
onClick(){
this.$emit('close')
}
}
}



